import React, { useState, useEffect } from 'react';
import { Card, List, Avatar, Button, Typography, message, Empty, Spin, Popconfirm } from 'antd';
import { PlayCircleOutlined, CalendarOutlined, FileOutlined, DeleteOutlined, LoadingOutlined } from '@ant-design/icons';
import { VideoInfo } from '../types';
import { formatFileSize, formatDuration } from '../utils/helpers';
import { azureStorageService } from '../services/azureStorageService';

const { Text } = Typography;

interface VideoListProps {
  onVideoSelect: (video: VideoInfo) => void;
  selectedVideoId?: string;
  refreshTrigger?: number; // 
  onVideoDelete?: (videoId: string) => void; // 
  onVideoIndexingUpdate?: (videoId: string, isIndexing: boolean, isIndexed: boolean) => void; // 
  pendingVideos?: VideoInfo[]; // 
}

const VideoList: React.FC<VideoListProps> = ({
  onVideoSelect,
  selectedVideoId,
  refreshTrigger,
  onVideoDelete,
  onVideoIndexingUpdate,
  pendingVideos = []
}) => {
  const [videos, setVideos] = useState<VideoInfo[]>([]);
  const [loading, setLoading] = useState(false);
  const [deletingVideoId, setDeletingVideoId] = useState<string | null>(null);

  // 
  const loadVideos = async () => {
    setLoading(true);
    try {
      const videoList = await azureStorageService.getVideoList();
      setVideos(videoList);
    } catch (error) {
      console.error(':', error);
      message.error('');
    } finally {
      setLoading(false);
    }
  };

  // 
  useEffect(() => {
    loadVideos();
  }, [refreshTrigger]);

  // 
  const formatUploadDate = (date: Date) => {
    const now = new Date();
    const diffTime = Math.abs(now.getTime() - date.getTime());
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    
    if (diffDays === 1) return '';
    if (diffDays === 2) return '';
    if (diffDays <= 7) return `${diffDays - 1} `;
    
    return date.toLocaleDateString('zh-TW');
  };

  // 
  const handleDeleteVideo = async (video: VideoInfo) => {
    setDeletingVideoId(video.id);
    
    try {
      console.log(':', video.name);
      await azureStorageService.deleteVideo(video.id);
      
      message.success(` "${video.name}" `);
      
      // 
      setVideos(prevVideos => prevVideos.filter(v => v.id !== video.id));
      
      // 
      if (selectedVideoId === video.id) {
        onVideoDelete?.(video.id);
      }
      
    } catch (error) {
      console.error(':', error);
      const errorMessage = error instanceof Error ? error.message : '';
      message.error(`: ${errorMessage}`);
    } finally {
      setDeletingVideoId(null);
    }
  };

  // 
  const updateVideoIndexingStatus = (videoId: string, isIndexing: boolean, isIndexed: boolean) => {
    setVideos(prevVideos => 
      prevVideos.map(video => 
        video.id === videoId 
          ? { ...video, isIndexing, isIndexed }
          : video
      )
    );
    onVideoIndexingUpdate?.(videoId, isIndexing, isIndexed);
  };

  // 
  const renderSelectButton = (video: VideoInfo) => {
    if (video.isIndexing) {
      return (
        <Button
          key="indexing"
          icon={<LoadingOutlined />}
          disabled
          size="small"
          style={{
            borderRadius: '20px',
            minWidth: '80px',
            height: '32px',
            backgroundColor: 'rgba(255, 193, 7, 0.1)',
            borderColor: 'rgba(255, 193, 7, 0.3)',
            color: '#ffc107'
          }}
        >
          
        </Button>
      );
    }

    if (!video.isIndexed) {
      return (
        <Button
          key="waiting"
          disabled
          size="small"
          style={{
            borderRadius: '20px',
            minWidth: '80px',
            height: '32px',
            backgroundColor: 'rgba(108, 117, 125, 0.1)',
            borderColor: 'rgba(108, 117, 125, 0.3)',
            color: '#6c757d'
          }}
        >
          
        </Button>
      );
    }

    return (
      <Button
        key="select"
        icon={<PlayCircleOutlined />}
        onClick={() => onVideoSelect(video)}
        size="small"
        style={{
          borderRadius: '20px',
          minWidth: '80px',
          height: '32px',
          backgroundColor: selectedVideoId === video.id 
            ? '#f6ffed' 
            : 'rgba(74, 158, 255, 0.1)',
          borderColor: selectedVideoId === video.id 
            ? '#52c41a' 
            : 'rgba(74, 158, 255, 0.3)',
          color: selectedVideoId === video.id 
            ? '#52c41a' 
            : '#4a9eff',
          fontWeight: '500',
          transition: 'all 0.3s ease',
          boxShadow: 'none'
        }}
      >
        {selectedVideoId === video.id ? '' : ''}
      </Button>
    );
  };

  // 
  const allVideos = React.useMemo(() => {
    // 
    const loadedVideosFiltered = videos.filter(
      video => !pendingVideos.some(pending => pending.id === video.id)
    );
    
    // 
    const combined = [...pendingVideos, ...loadedVideosFiltered];
    return combined.sort((a, b) => b.uploadDate.getTime() - a.uploadDate.getTime());
  }, [videos, pendingVideos]);

  if (loading) {
    return (
      <Card title="">
        <div style={{ textAlign: 'center', padding: '50px 0' }}>
          <Spin size="large" />
          <div style={{ marginTop: 16 }}>
            <Text type="secondary">...</Text>
          </div>
        </div>
      </Card>
    );
  }

  return (
    <Card 
      title="" 
      extra={
        <Button onClick={loadVideos} size="small">
          
        </Button>
      }
      style={{ 
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        position: 'relative',
        paddingTop: 2,
      }}
    >
      {allVideos.length === 0 ? (
        <Empty
          description=""
          image={Empty.PRESENTED_IMAGE_SIMPLE}
        />
      ) : (
        <div style={{ 
          flex: 1,
          overflow: 'auto'
        }}>
          <List
            itemLayout="horizontal"
            dataSource={allVideos}
            renderItem={(video) => (
              <List.Item
                actions={[
                  renderSelectButton(video),
                  <Popconfirm
                    key="delete"
                    title=""
                    description={` "${video.name}" `}
                    onConfirm={() => handleDeleteVideo(video)}
                    okText=""
                    cancelText=""
                    okType="danger"
                    disabled={video.isIndexing} // 
                  >
                    <Button
                      icon={<DeleteOutlined />}
                      loading={deletingVideoId === video.id}
                      disabled={deletingVideoId === video.id || video.isIndexing} // 
                      danger
                      size="small"
                      title={video.isIndexing ? '' : ''}
                      style={{
                        borderRadius: '20px',
                        minWidth: '80px',
                        height: '32px',
                        backgroundColor: 'rgba(255, 77, 79, 0.1)',
                        borderColor: 'rgba(255, 77, 79, 0.3)',
                        color: '#ff4d4f',
                        fontWeight: '500',
                        transition: 'all 0.3s ease'
                      }}
                    >
                      
                    </Button>
                  </Popconfirm>
                ]}
                style={{
                  backgroundColor: selectedVideoId === video.id ? '#f6ffed' : 'transparent',
                  padding: '12px 16px',
                  borderRadius: '6px',
                  margin: '4px 0',
                  border: selectedVideoId === video.id ? '1px solid #52c41a' : '1px solid transparent'
                }}
              >
                <List.Item.Meta
                  avatar={
                    <Avatar 
                      src={video.thumbnail} 
                      icon={<PlayCircleOutlined />}
                      size={64}
                      shape="square"
                      style={{
                        flexShrink: 0, // 
                        width: '64px',
                        height: '64px',
                        minWidth: '64px',
                        minHeight: '64px'
                      }}
                    />
                  }
                  title={
                    <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                      <Text strong style={{ fontSize: '14px' }}>
                        {video.name}
                      </Text>
                      {selectedVideoId === video.id && (
                        <Text type="success" style={{ fontSize: '12px' }}>
                           
                        </Text>
                      )}
                    </div>
                  }
                  description={
                    <div style={{ fontSize: '12px' }}>
                      <div style={{ display: 'flex', alignItems: 'center', gap: 12, marginBottom: 4 }}>
                        <span style={{ display: 'flex', alignItems: 'center', gap: 4 }}>
                          <FileOutlined />
                          <Text type="secondary">{formatFileSize(video.size)}</Text>
                        </span>
                        {video.duration && (
                          <span style={{ display: 'flex', alignItems: 'center', gap: 4 }}>
                            <PlayCircleOutlined />
                            <Text type="secondary">{formatDuration(video.duration)}</Text>
                          </span>
                        )}
                      </div>
                      <div style={{ display: 'flex', alignItems: 'center', gap: 4 }}>
                        <CalendarOutlined />
                        <Text type="secondary">
                           {formatUploadDate(video.uploadDate)}
                        </Text>
                      </div>
                    </div>
                  }
                />
              </List.Item>
            )}
          />
        </div>
      )}
    </Card>
  );
};

export default VideoList;

