import React, { useState } from 'react';
import { Layout, Row, Col, Typography, message, ConfigProvider, theme } from 'antd';
import { PlayCircleOutlined } from '@ant-design/icons';
import VideoUpload from './components/VideoUpload';
import VideoList from './components/VideoList';
import ChatInterface from './components/ChatInterface';
import { VideoInfo } from './types';
import { azureStorageService } from './services/azureStorageService';
import 'antd/dist/reset.css';

const { Header, Content } = Layout;
const { Title, Text } = Typography;

const App: React.FC = () => {
  const [selectedVideo, setSelectedVideo] = useState<VideoInfo | null>(null);
  const [refreshTrigger, setRefreshTrigger] = useState(0);
  const [pendingVideos, setPendingVideos] = useState<VideoInfo[]>([]); // 

  // 
  const handleUploadSuccess = async (video: VideoInfo) => {
    //  loading 
    message.destroy();
    message.success(` "${video.name}" `);
    
    // 
    const videoWithIndexing = { 
      ...video, 
      isIndexing: true, 
      isIndexed: false 
    };
    
    // 
    setPendingVideos(prev => [...prev, videoWithIndexing]);
    
    // 
    try {
      console.log('...');
      message.info(` "${video.name}"...`);
      
      //  API
      await azureStorageService.indexVideo(video.name);
      
      message.success(` "${video.name}" `);
      
      // 
      setPendingVideos(prev => prev.filter(v => v.id !== video.id));
      setRefreshTrigger(prev => prev + 1);
      
    } catch (error) {
      console.error(':', error);
      const errorMessage = error instanceof Error ? error.message : '';
      message.error(`: ${errorMessage}`);
      
      // 
      setPendingVideos(prev => 
        prev.map(v => 
          v.id === video.id 
            ? { ...v, isIndexing: false, isIndexed: true }
            : v
        )
      );
      
      // 
      setTimeout(() => {
        setPendingVideos(prev => prev.filter(v => v.id !== video.id));
        setRefreshTrigger(prev => prev + 1);
      }, 2000);
    }
  };

  // 
  const handleVideoSelect = (video: VideoInfo) => {
    setSelectedVideo(video);
    message.info(`: ${video.name}`);
  };

  // 
  const handleVideoDelete = (videoId: string) => {
    // 
    if (selectedVideo?.id === videoId) {
      setSelectedVideo(null);
      message.info('');
    }
  };

  // 
  const handleVideoIndexingUpdate = (videoId: string, isIndexing: boolean, isIndexed: boolean) => {
    // 
    if (selectedVideo?.id === videoId && isIndexing) {
      setSelectedVideo(null);
    }
  };

  // 
  const handleUploadStart = () => {
    //  duration: 0  loading 
    console.log('...');
  };

  // 
  const handleUploadError = (error: string) => {
    // 
    message.destroy();
    message.error(`: ${error}`);
  };

  return (
    <ConfigProvider
      theme={{
        algorithm: theme.darkAlgorithm,
        token: {
          colorPrimary: '#00ffff',
          borderRadius: 8,
          colorBgContainer: '#1a1a1a',
          colorBgElevated: '#1a1a1a',
          colorBgLayout: '#0a0a0a',
          colorText: '#ffffff',
          colorTextBase: '#ffffff',
          colorBorder: '#333333',
          colorBorderSecondary: '#555555',
        },
      }}
    >
      <Layout style={{ minHeight: '100vh', backgroundColor: '#0a0a0a' }}>
        {/*  */}
        <Header style={{ 
          backgroundColor: 'rgba(20, 20, 30, 0.9)', 
          padding: '0 24px',
          borderBottom: '1px solid rgba(70, 70, 90, 0.6)',
          display: 'flex',
          alignItems: 'center',
          boxShadow: '0 2px 15px rgba(0,0,0,0.2)'
        }}>
          <div style={{ display: 'flex', alignItems: 'center', gap: 12 }}>
            <PlayCircleOutlined style={{ 
              fontSize: '24px', 
              color: '#00d4aa',
              filter: 'drop-shadow(0 0 8px rgba(0, 212, 170, 0.5))',
              animation: 'iconGlow 3s ease-in-out infinite'
            }} />
            <Title level={3} style={{ 
              margin: 0, 
              color: '#ffffff'
            }}>
              
            </Title>
          </div>
          <div style={{ marginLeft: 'auto' }}>
            <Text type="secondary" style={{ color: '#bbb' }}>
               AI 
            </Text>
          </div>
        </Header>

        {/*  */}
        <Content style={{ 
          
          padding: '24px', 
          height: 'calc(100vh - 88px)', //  Header ,
        }}>
          <Row gutter={[24, 24]} style={{ height: '100%' }}>
            {/*  */}
            <Col xs={24} lg={10} style={{ height: '100%' }}>
              <div style={{ 
                display: 'flex', 
                flexDirection: 'column', 
                gap: 3,
                height: '100%',
              }}>
                {/*  -  */}
                <div style={{ 
                  flexShrink: 0,
                  height: 'auto',
                  paddingTop: 3,
                }}>
                  <VideoUpload
                    onUploadSuccess={handleUploadSuccess}
                    onUploadStart={handleUploadStart}
                    onUploadError={handleUploadError}
                  />
                </div>

                {/*  -  */}
                <div style={{ 
                  flex: 1,
                  minHeight: 0, //  flex 
                  overflow: 'hidden',
                  paddingTop: 3,
                }}>
                  <VideoList
                    onVideoSelect={handleVideoSelect}
                    selectedVideoId={selectedVideo?.id}
                    refreshTrigger={refreshTrigger}
                    onVideoDelete={handleVideoDelete}
                    onVideoIndexingUpdate={handleVideoIndexingUpdate}
                    pendingVideos={pendingVideos}
                  />
                </div>
              </div>
            </Col>

            {/*  -  */}
            <Col xs={24} lg={14} style={{ height: '100%' }}>
              <div style={{ height: '100%', paddingTop: 3 }}>
                <ChatInterface selectedVideo={selectedVideo} />
              </div>
            </Col>
          </Row>
        </Content>
      </Layout>
    </ConfigProvider>
  );
};

export default App;
