import { v4 as uuidv4 } from 'uuid';

// 
export const formatFileSize = (bytes: number): string => {
  if (bytes === 0) return '0 Bytes';
  
  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
};

// 
export const formatDuration = (seconds: number): string => {
  const hours = Math.floor(seconds / 3600);
  const minutes = Math.floor((seconds % 3600) / 60);
  const secs = Math.floor(seconds % 60);
  
  if (hours > 0) {
    return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  }
  return `${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
};

//  ID
export const generateId = (): string => {
  return uuidv4();
};

// 
export const validateVideoFile = (file: File): { isValid: boolean; error?: string } => {
  const supportedFormats = [
    'video/mp4',
    'video/mov',
    'video/avi',
    'video/mkv',
    'video/webm',
    'video/quicktime'
  ];
  
  if (!supportedFormats.includes(file.type)) {
    return {
      isValid: false,
      error: ' MP4MOVAVIMKV  WebM '
    };
  }
  
  const maxSize = 100 * 1024 * 1024; // 100MB
  if (file.size > maxSize) {
    return {
      isValid: false,
      error: ' 100MB'
    };
  }
  
  return { isValid: true };
};

// 
export const delay = (ms: number): Promise<void> => {
  return new Promise(resolve => setTimeout(resolve, ms));
};

//  JSON 
export const safeJsonParse = (jsonString: string, fallback: any = null): any => {
  try {
    // 
    if (!jsonString || !jsonString.trim()) {
      return fallback;
    }
    return JSON.parse(jsonString);
  } catch (error) {
    // 
    if (process.env.NODE_ENV === 'development') {
      console.warn('JSON :', {
        error: error,
        input: jsonString.substring(0, 100) + (jsonString.length > 100 ? '...' : '')
      });
    }
    return fallback;
  }
};

// 
export const truncateText = (text: string, maxLength: number): string => {
  if (text.length <= maxLength) return text;
  return text.substring(0, maxLength) + '...';
};
