import React, { useState, useRef } from 'react';
import { Upload, Button, Progress, message, Card, Typography } from 'antd';
import { InboxOutlined, DeleteOutlined } from '@ant-design/icons';
import { VideoInfo, UploadProgress } from '../types';
import { validateVideoFile, formatFileSize } from '../utils/helpers';
import { azureStorageService } from '../services/azureStorageService';

const { Dragger } = Upload;
const { Text } = Typography;

interface VideoUploadProps {
  onUploadSuccess: (video: VideoInfo) => void;
  onUploadStart?: () => void;
  onUploadError?: (error: string) => void;
}

const VideoUpload: React.FC<VideoUploadProps> = ({
  onUploadSuccess,
  onUploadStart,
  onUploadError
}) => {
  const [uploading, setUploading] = useState(false);
  const [progress, setProgress] = useState<UploadProgress | null>(null);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  // 
  const handleFileSelect = (file: File) => {
    const validation = validateVideoFile(file);
    if (!validation.isValid) {
      message.error(validation.error);
      return false;
    }

    setSelectedFile(file);
    message.success(`: ${file.name}`);
    return false; //  antd 
  };

  // 
  const startUpload = async () => {
    if (!selectedFile) {
      message.warning('');
      return;
    }

    setUploading(true);
    setProgress({ loaded: 0, total: selectedFile.size, percentage: 0 });
    onUploadStart?.();

    try {
      console.log(':', selectedFile.name);
      
      const videoInfo = await azureStorageService.uploadVideo(
        selectedFile,
        (uploadProgress) => {
          console.log(':', uploadProgress);
          setProgress(uploadProgress);
        }
      );

      console.log(':', videoInfo);
      
      //  false
      setUploading(false);
      
      // 
      setTimeout(() => {
        onUploadSuccess(videoInfo);
        
        // 
        setSelectedFile(null);
        setProgress(null);
      }, 1000);
      
    } catch (error) {
      console.error(':', error);
      const errorMessage = error instanceof Error ? error.message : '';
      message.error(errorMessage);
      onUploadError?.(errorMessage);
      
      // 
      setUploading(false);
      setProgress(null);
    }
  };

  // 
  const clearSelectedFile = () => {
    setSelectedFile(null);
    setProgress(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  return (
    <Card title="" style={{ marginBottom: 6, position: 'relative' }}>
      <div style={{ marginBottom: 16, paddingTop: 3 }}>
        <Dragger
          name="video"
          multiple={false}
          beforeUpload={handleFileSelect}
          showUploadList={false}
          disabled={uploading}
          accept="video/*"
        >
          <p className="ant-upload-drag-icon">
            <InboxOutlined />
          </p>
          <p className="ant-upload-text">
            
          </p>
          <p className="ant-upload-hint">
             MP4MOVAVIMKVWebM  100MB
          </p>
        </Dragger>
      </div>

      {selectedFile && (
        <Card size="small" style={{ marginBottom: 16 }}>
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <div>
              <Text strong>{selectedFile.name}</Text>
              <br />
              <Text type="secondary">{formatFileSize(selectedFile.size)}</Text>
            </div>
            <Button
              icon={<DeleteOutlined />}
              onClick={clearSelectedFile}
              disabled={uploading}
              danger
              size="small"
            >
              
            </Button>
          </div>
        </Card>
      )}

      {progress && (
        <div style={{ marginBottom: 16 }}>
          <Progress
            percent={Math.round(progress.percentage)}
            status={uploading ? 'active' : 'success'}
            showInfo
          />
          <Text type="secondary">
            {formatFileSize(progress.loaded)} / {formatFileSize(progress.total)}
          </Text>
        </div>
      )}

      <Button
        type="primary"
        onClick={startUpload}
        loading={uploading}
        disabled={!selectedFile || uploading}
        size="large"
        block
      >
        {uploading ? '...' : ''}
      </Button>
    </Card>
  );
};

export default VideoUpload;
