import { ChatMessage, StreamEvent } from '../types';
import { safeJsonParse } from '../utils/helpers';
import { getConfig } from '../utils/config';

const config = {
  azureOpenAI: {
    endpoint: getConfig('AZURE_OPENAI_ENDPOINT'),
    apiKey: getConfig('AZURE_OPENAI_API_KEY'),
    deploymentName: getConfig('AZURE_OPENAI_DEPLOYMENT_NAME') || 'gpt-4o',
    apiVersion: getConfig('AZURE_OPENAI_API_VERSION') || '2024-10-21',
  },
  //  mock  API 
};

class AzureOpenAIService {
  private readonly tools = [
    {
      type: 'function',
      function: {
        name: 'query_video_transcription',
        description: '',
        parameters: {
          type: 'object',
          properties: {
            query: {
              type: 'string',
              description: ''
            }
          },
          required: ['query']
        }
      }
    }
  ];

  //  API
  private async callTranscriptionAPI(query: string, videoId: string): Promise<string> {
    console.log(' []  API:', { query, videoId });
    
    try {
      // 
      const { transcriptionService } = await import('./transcriptionService');
      
      console.log(' [] ...');
      const response = await transcriptionService.queryTranscription({
        query,
        videoId
      });
      
      console.log(' []  API :', {
        responseLength: response.text.length,
        responsePreview: response.text.substring(0, 100) + '...'
      });
      
      return response.text;
    } catch (error) {
      console.error(' []  API :', error);
      const fallbackMessage = ` "${query}" `;
      console.log(' [] :', fallbackMessage);
      return fallbackMessage;
    }
  }

  //  Azure OpenAI API 
  private async *streamFromAzureOpenAI(
    messages: any[],
    videoId: string
  ): AsyncGenerator<StreamEvent> {
    console.log(' [LLM]  Azure OpenAI ');
    console.log(' [LLM] :', {
      endpoint: config.azureOpenAI.endpoint,
      deployment: config.azureOpenAI.deploymentName,
      messagesCount: messages.length,
      videoId,
      hasTools: this.tools.length > 0
    });
    
    const url = `${config.azureOpenAI.endpoint}/openai/deployments/${config.azureOpenAI.deploymentName}/chat/completions?api-version=${config.azureOpenAI.apiVersion}`;
    
    const requestBody = {
      messages,
      tools: this.tools,
      tool_choice: 'auto',
      stream: true,
      temperature: 0.7,
      max_tokens: 1000
    };

    console.log(' [LLM] :', url);
    console.log(' [LLM] :', {
      ...requestBody,
      messages: `${messages.length} `
    });

    try {
      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'api-key': config.azureOpenAI.apiKey
        },
        body: JSON.stringify(requestBody)
      });

      if (!response.ok) {
        console.error(' [LLM] Azure OpenAI API :', {
          status: response.status,
          statusText: response.statusText,
          url: response.url
        });
        throw new Error(`Azure OpenAI API : ${response.status}`);
      }

      console.log(' [LLM] API ');

      const reader = response.body?.getReader();
      if (!reader) {
        console.error(' [LLM] ');
        throw new Error('');
      }

      console.log(' [LLM] ...');

      const decoder = new TextDecoder();
      let buffer = '';
  let toolCallId = '';
  let toolCallName = '';
  let toolCallArgs = '';

      while (true) {
        const { done, value } = await reader.read();
        if (done) break;

        buffer += decoder.decode(value, { stream: true });
        const lines = buffer.split('\n');
        buffer = lines.pop() || '';

        for (const line of lines) {
          if (line.startsWith('data: ')) {
            const data = line.slice(6);
            if (data === '[DONE]') continue;

            try {
              const parsed = safeJsonParse(data, null);
              if (!parsed) {
                console.warn(' [LLM]  SSE :', data.substring(0, 100));
                continue;
              }
              
              const choice = parsed.choices?.[0];
              if (!choice) continue;

              const delta = choice.delta;

              // 
              if (delta.tool_calls) {
                const toolCall = delta.tool_calls[0];
                if (toolCall.id) {
                  toolCallId = toolCall.id;
                  console.log(' [LLM] ID:', toolCallId);
                }
                if (toolCall.function?.name) {
                  toolCallName = toolCall.function.name;
                  console.log(' [LLM] :', toolCallName);
                }
                if (toolCall.function?.arguments) {
                  toolCallArgs += toolCall.function.arguments;
                  console.log(' [LLM] :', toolCall.function.arguments);
                }
                // 
                const partialArgs = safeJsonParse(toolCallArgs || '{}', {});
                yield { type: 'tool_call', status: 'start', name: toolCallName, args: partialArgs };
              }

              // 
              if (choice.finish_reason === 'tool_calls' && toolCallName === 'query_video_transcription') {
                console.log(' [LLM] ');
                console.log(' [LLM] :', toolCallArgs);
                
                const args = safeJsonParse(toolCallArgs, {});
                console.log(' [LLM] :', args);
                
                // end  LLM 
                yield { type: 'tool_call', status: 'end', name: toolCallName, args };

                const transcriptionResult = await this.callTranscriptionAPI(args.query || '', videoId);
                
                console.log(' [LLM] ');
                console.log(' [LLM] :', transcriptionResult.substring(0, 150) + '...');

                // 
                const newMessages = [
                  ...messages,
                  {
                    role: 'assistant',
                    tool_calls: [{
                      id: toolCallId,
                      type: 'function',
                      function: {
                        name: toolCallName,
                        arguments: toolCallArgs
                      }
                    }]
                  },
                  {
                    role: 'tool',
                    tool_call_id: toolCallId,
                    content: transcriptionResult
                  }
                ];

                console.log(' [LLM] ');
                // 
                yield* this.streamFromAzureOpenAI(newMessages, videoId);
                return;
              }

              // 
              if (delta.content) {
                // 
                if (delta.content.length > 5) {
                  console.log(' [LLM] :', delta.content.substring(0, 50) + '...');
                }
                yield { type: 'text', content: delta.content };
              }
            } catch (e) {
              console.error(' [LLM]  SSE :', e);
              console.error(' [LLM] :', line);
            }
          }
        }
      }
    } catch (error) {
      console.error(' [LLM] Azure OpenAI API :', error);
      console.log(' [LLM] ');
  yield { type: 'text', content: `` };
    }
  }

  // 
  async *askQuestion(
    question: string,
    videoId: string,
    chatHistory: ChatMessage[] = []
  ): AsyncGenerator<StreamEvent> {
    console.log(' [] :', question);
    console.log(' []  ID:', videoId);
    console.log(' [] :', chatHistory.length);
  console.log(' []  OpenAI');
  console.log(' []  API Key:', !!config.azureOpenAI.apiKey);
    
  const systemMessage = {
      role: 'system',
      content: ''
    };

    const messages = [
      systemMessage,
      {
        role: 'user',
        content: question
      },
      ...chatHistory
        .filter(msg => msg.role === 'user' || msg.role === 'assistant')
        .map(msg => ({
          role: msg.role,
          content: msg.content
        })),
      {
        role: 'user',
        content: question
      }
    ];

    console.log(' [] :', messages.length);

  //  Azure OpenAI 
  yield* this.streamFromAzureOpenAI(messages, videoId);
    
    console.log(' [] ');
  }
}

export const azureOpenAIService = new AzureOpenAIService();
